<template>
  <div class="login">
    <div class="l-box">
      <div class="inp-box">
        <input class="scale-border" name="account" type="text" placeholder="输入卡号或手机号">
        <!--<van-icon class="close-1" name="clear" />-->
      </div>

      <span class="scaleBorder">
           </span>
      <div class="inp-box">
        <input type="text" name="password" placeholder="输入密码">
        <!--<van-icon class="close-2" name="clear" />-->
      </div>
      <span class="scaleBorder">
      </span>
      <div class="inp-box">
        <input type="text" name="yanzm" placeholder="输入验证码">
        <!--<van-icon class="close-2" name="clear" />-->
        <span class="time" @click="yan">{{time}}</span>
      </div>

    </div>
    <van-button class="lbtn" tag="a" size="large" type="primary">登录</van-button>
    <div class="other">
          <span>
            手机号
            <a href="#">在线申请</a>
          </span>
      <span>
            <a href="#">忘记密码</a>
          </span>
    </div>
    <div class="shade" v-if="tishi">
      <img class="icon-xq" src="../assets/img/icon_1.png" alt="">
      <span>验证码已发送至注册手机，请输入验证码</span>
    </div>
    <van-icon class="arrow" name="arrow" />

  </div>
</template>
<script>
  import "lib-flexible";
  export default {
    data() {
      return {
        tishi: false,
        time: '获取验证码'
      }
    },
    methods: {
      yan: function () {
        this.tishi = true;
        var that = this;
        var timer = setInterval(function () {
          if (that.time === '获取验证码') {
            that.time = 60;
          }else if (that.time > 0) {
            that.time--
          }else {
            clearInterval(timer)
            that.time = '获取验证码'
          }
          if(that.time === 58){
            that.tishi = false
          }
        }, 1000)
      }
    }
  }
</script>
<style lang='less' scoped>
  .wh(@w, @h) {
    width: @w;
    height: @h;
  }

  .dpl {
    display: inline-block;
    vertical-align: middle;
  }

  .auto {
    left: 0;
    right: 0;
    margin: 0 auto;
  }

  .login {
    position: relative;
    background: url("../assets/img/bg.jpg") no-repeat;
    background-size: 100% 100%;
    .wh(100%,
    100%);
    .l-box {
      position: absolute;
      bottom: 427px;
      padding: 20px 40px;
      background: #fff;
      border-radius: 3px;
      box-sizing: border-box;
      font-size: 24px;
      .auto;
      .wh(644px,
      auto);
      input {
        display: block;
        width: 100%;
        height: 80px;
        line-height: 40px;
        font-size: 30px;
        outline: none;
        border: none;
        color: #666;
      }
      .inp-box {
        position: relative;
        width: 100%;
        height: 80px;
        .close-1,
        .close-2 {
          display: none;
          position: absolute;
          top: 12px;
          right: 0;
          .wh(15px,
          15px)
        }
        .time {
          position: absolute;
          top: 30px;
          right: 30px;
          font-size: 24px;
        }
      }
      input::-webkit-input-placeholder {
        color: #bcbcbc;
      }
      .scaleBorder {
        position: relative;
        content: "";
        display: block;
        background: #eee;
        transform: scaleY(.6);
        .wh(100%,
        1px);
      }
    }
    .lbtn {
      position: absolute;
      bottom: 294px;
      background: #893dbd;
      outline: none;
      border: none;
      .auto;
      .wh(643px,
      98px)
    }
    .other {
      position: absolute;
      bottom: 235px;
      overflow: hidden;
      padding: 0 12px;
      color: #bcbcbc;
      font-size: 14px;
      box-sizing: border-box;
      .auto;
      .wh(644px,
      auto);
      span:nth-of-type(2) {
        float: right;
      }
      a {
        color: #a76fcd;
      }
    }
    .shade {
      position: absolute;
      bottom: 450px;
      background: url("../assets/img/shade.png") no-repeat;
      background-size: 100% 100%;
      font-size: 28px;
      text-align: center;
      padding: 36px 0 16px;
      box-sizing: border-box;
      .wh(661px,
      238px);
      .auto;
      .icon-xq {
        display: block;
        margin: 0 auto 34px;
        .wh(64px,
        64px)
      }
      span {
        display: block;
        font-size: 28px;
        color: #000;
      }
    }
    .arrow {
      position: absolute;
      bottom: 25px;
      transform: rotate(-90deg);
      color: #893dbd;
      font-size: 34px;
      .auto;
      .wh(17px,
      12px)
    }
  }
</style>



